html {
  scroll-behavior: smooth;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: stretch;
}

.main-content {
  flex: 1;
  width: 100%;
}
.main-content > *:first-of-type > *:first-child:not(.full-width) {
  margin-top: var(--tusk-header-height);
  padding-top: var(--tusk-margin-400);
}
.main-content h1 + .body-text, .main-content h2 + .body-text, .main-content h3 + .body-text, .main-content h4 + .body-text, .main-content h5 + .body-text, .main-content h6 + .body-text {
  margin-top: var(--tusk-margin-200);
}

.hidden {
  display: none;
}

.box {
  /* ↓ Padding set to the first point on the modular scale */
  padding: 2rem;
  border: 0 none solid;
  /* ↓ Always apply the transparent outline, for high contrast mode */
  outline: 0 transparent;
  outline-offset: calc(0 * -1);
  color: var(--tusk-text-color);
  background-color: var(--tusk-background-color);
  border-radius: var(--tusk-border-radius);
}
.box * {
  /* ↓ Force colors to inherit from the parent
  and honor inversion (below) */
  color: inherit;
}

.box--inverted, .box--alt {
  /* ↓ Padding set to the first point on the modular scale */
  padding: 2rem;
  border: 0 var(--tusk-background-color) solid;
  /* ↓ Always apply the transparent outline, for high contrast mode */
  outline: 0 transparent;
  outline-offset: calc(0 * -1);
  color: var(--tusk-text-color);
  background-color: var(--tusk-contrast-color);
  border-radius: var(--tusk-border-radius);
}
.box--inverted *, .box--alt * {
  /* ↓ Force colors to inherit from the parent
  and honor inversion (below) */
  color: inherit;
}

.inverted {
  --primary-color: color("secondary");
  --secondary-color: color("primary");
  --background-color: color("contrast");
  --text-color: color("background");
  --contrast-color: color("text");
}

.cluster {
  gap: 1rem;
  display: flex;
  flex-wrap: wrap;
}

.cluster--none, .cluster--0 {
  gap: 0;
  display: flex;
  flex-wrap: wrap;
}

.cluster--100 {
  gap: var(--tusk-margin-100);
  display: flex;
  flex-wrap: wrap;
}

.cluster--200 {
  gap: var(--tusk-margin-200);
  display: flex;
  flex-wrap: wrap;
}

.cluster--600 {
  gap: var(--tusk-margin-200);
  display: flex;
  flex-wrap: wrap;
}

.cluster-end {
  justify-content: flex-end;
}

.center {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.stack > * + * {
  margin-top: var(--tusk-margin-400);
}

.stack--200 > * + * {
  margin-top: var(--tusk-margin-200);
}

.stack--300 > * + * {
  margin-top: var(--tusk-margin-300);
}

.stack--600 > * + * {
  margin-top: var(--tusk-margin-600);
}

.icon {
  display: inline-block;
  height: 100%;
}
.icon svg {
  display: inline-block;
  height: 100%;
  width: auto;
  vertical-align: text-top;
  margin: 1px;
  overflow: visible;
}

.with-icon {
  /* ↓ Set the `inline-flex` context,  which eliminates the word space */
  display: inline-flex;
  align-items: center;
  gap: var(--tusk-space, 0.5rem);
}

.cover {
  object-position: center;
  object-fit: cover;
  height: 100%;
}

.sr-only {
  display: inline-block;
  opacity: 0;
  background: none;
  position: absolute;
  line-height: 0;
  font-size: 0;
  overflow: hidden;
  width: 2px;
  height: 2px;
  margin: -1px;
}
.sr-only:focus, .sr-only:active {
  opacity: 1;
  font-size: inherit;
  line-height: inherit;
  width: auto;
  width: max-content;
  height: auto;
  height: max-content;
  margin: 0;
  z-index: 1;
}

.outer-bound {
  box-sizing: content-box;
  max-width: var(--tusk-outer-bound-width);
  margin-left: auto;
  margin-right: auto;
  padding-left: var(--tusk-outer-bound);
  padding-right: var(--tusk-outer-bound);
}

.inner-bound {
  box-sizing: content-box;
  max-width: var(--tusk-inner-bound-width);
  margin-left: auto;
  margin-right: auto;
  padding-left: var(--tusk-outer-bound);
  padding-right: var(--tusk-outer-bound);
}

.full-width {
  --half-screen-width: calc((100vw - var(--tusk-scrollbarWidth)) / 2);
  margin-left: calc(50% - var(--tusk-half-screen-width));
  margin-right: calc(50% - var(--tusk-half-screen-width));
}

.list-style-none {
  list-style: none;
}
.list-style-none li {
  position: static;
  padding: 0;
}
.list-style-none li::before {
  content: none;
  display: none;
}

img {
  width: 100%;
}

.background {
  background-color: var(--tusk-background-color);
  color: var(--tusk-contrast-color);
}

a, button, input, select, textarea {
  font: inherit;
  cursor: pointer;
  color: inherit;
}
a:focus, button:focus, input:focus, select:focus, textarea:focus {
  outline: none;
}

input, textarea, select, input[type=file] {
  background-color: var(--tusk-contrast-color);
  color: var(--tusk-text-color);
  border: var(--tusk-border-width) var(--tusk-border-type) var(--tusk-border-color);
  border-radius: var(--tusk-border-radius);
  font-size: 1rem;
  line-height: 1.55em;
  margin-bottom: 0rem;
  padding: 0.25em 0.5em;
}

input[type=text], textarea {
  cursor: text;
}

input[type=checkbox] {
  width: auto;
  display: inline-block;
}

label {
  font-weight: 600;
}

.input {
  display: flex;
  flex-direction: column;
  min-width: 32ex;
  width: fit-content;
  align-items: flex-start;
  gap: 0.25rem;
}

.checkbox {
  display: flex;
  gap: 1rem;
}

.button, .button-container a, button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
  padding: var(--tusk-margin-100) var(--tusk-margin-300);
  color: var(--tusk-contrast-color);
  background-color: var(--tusk-primary-color);
  text-align: center;
  text-decoration: none;
  border-width: var(--tusk-border-width);
  border-style: solid;
  border-color: var(--tusk-primary-color);
  border-radius: calc(var(--tusk-border-radius) * 0.5);
  outline: none;
  display: inline-flex;
  align-items: baseline;
  font-weight: 600;
}
.button::-moz-focus-inner, .button-container a::-moz-focus-inner, button::-moz-focus-inner {
  border: none;
}
.button:active, .button-container a:active, button:active {
  transform: translateY(1px);
