  line-height: 1.451486054em;
  margin-bottom: 0rem;
  font-weight: 600;
  color: var(--tusk-background-color);
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
}

.tiles {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: var(--tusk-margin-300);
}

.tile {
  position: relative;
}
.tile img {
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.tile__caption {
  position: absolute;
  bottom: var(--tusk-margin-200);
  left: var(--tusk-margin-300);
  right: var(--tusk-margin-300);
  font-size: 1.265625rem;
  line-height: 1.224691358em;
  margin-bottom: 0rem;
  font-weight: 600;
  color: var(--tusk-background-color);
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
}

.expander .body-text {
  margin-top: var(--tusk-margin-300);
}

.cards {
  display: flex;
  flex-flow: row;
  gap: var(--tusk-margin-400);
}

.card {
  flex: 1 1 0;
}

.light-box {
  overflow: hidden;
}
.light-box figure {
  height: 100%;
  width: 100%;
}
.light-box img,
.light-box video {
  height: 100%;
  width: 100%;
  object-fit: contain;
  object-position: center;
  max-height: 80vh;
}
.light-box figure {
  transition: 500ms ease-out;
  transform-origin: center;
  opacity: 0;
  transform: translateY(-10%);
}
.light-box--active {
  z-index: 10;
  opacity: 1;
}
.light-box--active figure {
  opacity: 1;
  transform: translateY(0);
}

.light-box__controlls {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.265625rem;
  line-height: 1.224691358em;
  margin-bottom: 0rem;
  font-variant-numeric: tabular-nums;
}
.light-box__controlls button {
  cursor: pointer;
  display: block;
  border: none;
  background: none;
  color: inherit;
}
.light-box__controlls svg {
  display: block;
  height: 2em;
  width: 2em;
  fill: currentColor;
}
.light-box__controlls .light-box--total::after {
  content: " | ";
}

.map .mapboxgl-marker svg path {
  fill: var(--tusk-primary-color, blue) !important;
}

body {
  display: grid;
  grid-template-columns: 100%;
}

.frame {
  display: flex;
}
.frame__sidebar {
  flex: 0 0 auto;
  display: flex;
  background-color: var(--tusk-contrast-color-400);
  flex-direction: column;
  gap: 2rem;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.1607843137);
  max-height: 100vh;
  min-width: 19rem;
  position: relative;
  z-index: 800;
  overflow-y: auto;
  overflow-x: hidden;
}
.frame__sidebar .logo {
  display: flex;
  justify-content: center;
  margin: 3rem 3rem;
  margin-bottom: 0;
  height: 12rem;
}
.frame__sidebar .logo svg {
  margin: auto;
  width: 12rem;
  height: auto;
}
.frame__sidebar > *:last-child {
  margin-top: auto;
  margin-bottom: 1rem;
}
.frame__content {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  overflow: auto;
  max-height: 100vh;
  position: relative;
}

.nav-block {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.nav-block__label {
  padding: 0 2rem;
  font-weight: 600;
  font-size: 0.8888888889rem;
  line-height: 1.74375em;
  margin-bottom: 0rem;
  color: var(--tusk-background-color-300);
}
.nav-block__list {
  display: flex;
  flex-direction: column;
  justify-content: stretch;
  align-items: stretch;
}
.nav-block__item a, .nav-block__item summary {
  background-color: transparent;
  border-color: transparent;
  color: var(--tusk-text-color);
  width: 100%;
  padding: 0.75rem 2rem;
  line-height: 1;
  font-weight: 600;
}
.nav-block__item a[aria-current]:hover, .nav-block__item summary[aria-current]:hover {
  color: var(--tusk-text-color);
}
.nav-block__item a:hover, .nav-block__item summary:hover {
  border-color: transparent;
}
.nav-block__item a[aria-current] {
  background-color: var(--tusk-primary-color);
}
.nav-block__item summary[aria-current] {
  background-color: var(--tusk-primary-color-200);
}
.nav-block__item summary::after {
  content: "▼";
  display: block;
  margin-left: auto;
  align-self: center;
  font-size: 0.7em;
}
.nav-block__item details[open] ul {
  border-left: solid var(--tusk-primary-color-200) 1rem;
  list-style: none;
}
.nav-block__item details[open] li::marker {
  color: transparent;
}
.nav-block__item details[open] summary {
  background-color: var(--tusk-primary-color-200);
  color: var(--tusk-text-color);
}
.nav-block__item details[open] summary::after {
  transform: rotate(180deg);
}
.nav-block__item .button {
  border-radius: 0;
}
.nav-block__item .button--icon {
  display: inline-flex;
  flex-direction: row;
  align-items: flex-end;
  gap: 0.75em;
}
.nav-block__item .button--icon svg {
  height: 1.2em;
  width: auto;
  margin-top: 0.2%;
  margin-bottom: -0.2%;
}

.login {
  background-color: var(--tusk-background-color);
}
.login .main-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.login .login-logo svg {
  width: 100%;
  height: auto;
}

.pages .page-list {
  list-style: none;
}
.pages .page-list .page-item {
  padding: 0.5rem 0 0 1rem;
  background-color: var(--tusk-contrast-color);
}
.pages .page-list .page-item__body {
  display: flex;
  justify-content: space-between;
}
.pages .page-list .page-item .page-item {
  margin-left: 1rem;
}
.pages .page-list li:nth-of-type(2n - 1) {
  background-color: var(--tusk-primary-color-200);
}
.pages .page-list li:nth-of-type(2n - 1) li:nth-of-type(2n - 1) {
  background-color: var(--tusk-contrast-color);
}
.pages .page-list li:nth-of-type(2n - 1) li:nth-of-type(2n - 1) li:nth-of-type(2n - 1) {
  background-color: var(--tusk-primary-color-200);
}
.pages .page-list li:nth-of-type(2n - 1) li:nth-of-type(2n - 1) li:nth-of-type(2n - 1) li:nth-of-type(2n - 1) {
  background-color: var(--tusk-contrast-color);
}
.pages .page-list li:nth-of-type(2n - 1) li:nth-of-type(2n - 1) li:nth-of-type(2n - 1) li:nth-of-type(2n - 1) li:nth-of-type(2n - 1) {
  background-color: var(--tusk-primary-color-200);
}
.pages .page-list ul {
  list-style: none;
}

.layout-element {
  border: solid 4px var(--tusk-primary-color) !important;
}

#close-modal {
  background-color: unset;
  border: none;
  display: block;
  height: 2rem;
  padding: 0.4rem;
  color: currentColor;
}
#close-modal svg {
  fill: currentColor;
  display: block;
  width: auto;
  height: 1rem;
}

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgb(255, 255, 255);
  min-width: 42rem;
  max-width: 100vw;
  resize: both;
  overflow: auto;
  border-radius: 0.25rem;
  border: none;
}
.modal::backdrop {
  background-color: rgba(0, 0, 0, 0.5);
}
.modal-header {
  display: flex;
  justify-content: space-between;
  background-color: black;
  color: #FFF;
  padding-left: 0.5rem;
  font-weight: 600;
}
.modal-main {
  padding: 1rem;
  justify-self: stretch;
  align-self: stretch;
}
.modal-form {
  display: grid;
  gap: 2rem;
}
.modal-form legend {
  display: none;
}

.main-header {
  background-color: var(--tusk-text-color);
  color: var(--tusk-contrast-color);
  padding: 1rem;
  display: flex;
}
.main-header > div {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#main-nav {
  transition: transform 250ms ease-in-out;
}
@media (max-width: 1200px) {
  #main-nav {
    transform: translateX(-100%);
    position: absolute;
    top: 0;
    bottom: 0;
  }
  #main-nav.nav--open {
    transform: translateX(0);
  }
}

#menu-button {
  position: relative;
}
@media (min-width: 1200px) {
  #menu-button {
    display: none;
  }
}
#menu-button .icon {
  position: absolute;
  right: 0;
}
#menu-button .cross {
  opacity: 0;
}

.mobile-nav-is-open #menu-button .cross {
  opacity: 1;
}
.mobile-nav-is-open #menu-button .menu {
  opacity: 0;
}

#home:focus {
  filter: brightness(0.8);
}

.pill {
  display: flex;
  gap: var(--tusk-border-width);
}
.pill :first-child:not(:only-child) {
  border-top-right-radius: 0 !important;
  border-bottom-right-radius: 0 !important;
}
.pill :last-child:not(:only-child) {
  border-top-left-radius: 0 !important;
  border-bottom-left-radius: 0 !important;
}
.pill *:not(:only-child):not(:last-child):not(:first-child) {
  border-radius: 0 !important;
}

